body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
### Primary
- Scissors Gradient: hsl(39, 89%, 49%) to hsl(40, 84%, 53%)
- Paper Gradient: hsl(230, 89%, 62%) to hsl(230, 89%, 65%)
- Rock Gradient: hsl(349, 71%, 52%) to hsl(349, 70%, 56%)
- Lizard Gradient: hsl(261, 73%, 60%) to hsl(261, 72%, 63%)
- Cyan: hsl(189, 59%, 53%) to hsl(189, 58%, 57%)
*/
button::-moz-focus-inner {
  border: 0; }

/* COLORS */
html {
  height: 100%;
  overflow: hidden;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#1f3756), to(#141539));
  background-image: linear-gradient(#1f3756, #141539); }

body {
  height: 100%;
  font-family: 'Barlow Semi Condensed', sans-serif;
  color: white;
  overflow: auto; }

/* .goog-te-spinner-pos+div {
  height: auto !important;
} */
#root {
  height: 100%; }

.header {
  width: 50%;
  margin: 30px 0;
  grid-area: header;
  display: grid;
  justify-self: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-template-areas: "title points";
  border: 3px solid #606e85;
  border-radius: 1rem;
  font-weight: 700;
  padding: 20px;
  padding-top: 10px; }

.title {
  grid-area: title;
  justify-self: left;
  align-self: center; }
  .title .logoImg {
    height: calc(20px + 6vw); }

.pointsContainer {
  grid-area: points;
  border-radius: 5px;
  justify-self: right;
  padding: 10px 40px 0 40px;
  background-color: #fff;
  font-weight: 600;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: flex-start; }

.points-title,
.points-count {
  margin: 0;
  padding: 0; }

.points-title {
  color: #2a46c0;
  font-size: calc(8px + .4vw); }

.points-count {
  text-align: center;
  color: #3b4363;
  font-size: calc(10px + 5vw); }

.chooseOption {
  grid-area: chooseOption;
  background: url(/Rock-Paper-Scissors-Game/static/media/bg-triangle.1fd44bdf.svg) no-repeat center; }

.paperElement,
.rockElement,
.scissorsElement {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  padding: 20px;
  display: flex;
  box-shadow: inset 0px -8px 0px 2px rgba(0, 0, 0, 0.5); }

.paperElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#4865f4), to(#5671f5));
  background-image: linear-gradient(#4865f4, #5671f5);
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-150%);
          transform: translateX(-150%); }

.rockElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#dc2e4e), to(#dd405d));
  background-image: linear-gradient(#dc2e4e, #dd405d);
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-25%);
          transform: translateX(-50%) translateY(-25%); }

.scissorsElement {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ec9e0e), to(#eca922));
  background-image: linear-gradient(#ec9e0e, #eca922);
  position: relative;
  left: 50%;
  -webkit-transform: translateX(50%) translateY(-100%);
          transform: translateX(50%) translateY(-100%); }

.chooseElementButton {
  cursor: pointer;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  background-color: white;
  border: 0px;
  margin: auto;
  outline: none;
  box-shadow: inset 0px 8px 0px -2px rgba(0, 0, 0, 0.1); }
  .chooseElementButton:hover {
    box-shadow: inset 0px 0px 15px rgba(0, 0, 0, 0.5); }
  .chooseElementButton:focus {
    box-shadow: inset 0px 0px 15px rgba(0, 0, 0, 0.9); }

.rulesContainer {
  display: flex;
  justify-content: flex-end;
  margin-right: 80px; }

.attribution {
  padding-top: 20px;
  text-align: center; }
  .attribution a:hover {
    color: white; }

/*=============================
          Media Queries 
 ============================*/
@media only screen and (max-width: 768px) {
  .chooseOption {
    padding-top: 50px;
    padding-bottom: 40px; }
  .paperElement {
    -webkit-transform: translateX(-160%) translateY(-40%);
            transform: translateX(-160%) translateY(-40%); }
  .scissorsElement {
    -webkit-transform: translateX(60%) translateY(-140%);
            transform: translateX(60%) translateY(-140%); }
  .rockElement {
    -webkit-transform: translateX(-50%) translateY(-12%);
            transform: translateX(-50%) translateY(-12%); }
  .paperElement,
  .rockElement,
  .scissorsElement {
    width: calc(55px + 1vw);
    height: calc(55px + 1vw); }
  .paperImg,
  .rockImg,
  .scissorsImg {
    height: calc(30px + 1vw); }
  .chooseElementButton {
    width: calc(55px + 1vw);
    height: calc(55px + 1vw); }
  .pointsContainer {
    padding: 0px 20px 0 20px; }
  .rulesContainer {
    justify-content: center;
    margin-right: 0px; }
  .attribution {
    padding-bottom: 5px; } }

